.match__finished{
    position: relative;
    margin:1rem;
    background: $dark_gray;
    width: 45rem;
    // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2), 0 3px 1px -2px rgba(0,0,0,0.3), 0 1px 5px 0 rgba(0,0,0,0.2);
    font-family: $anton;
    color: #555555;
    padding:1rem;
    height:20.7rem;
  
    
}

.match_top{
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    height:12.7rem;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    
}


.match_bottom{
    margin-top:0.4rem;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    font-size:1.8rem;
    text-transform: uppercase;

}
.match_bottom_text{
    margin:1rem 0 0 0.5rem;
    display: flex;
    flex-direction: row;
    justify-content:flex-start;
    
}
.match_bottom_text_pink{
    color:$salmon;
   
    margin-right: 1rem;
}

